<template>
  <div>
    <div id="title">
      <img src="../../../../static/img/back.png" @click="back()">
      <span></span>
    </div>
    <div id="center">
      <input v-model="username" placeholder="" type="text">
      <br>
      <input v-model="password" placeholder="" :type="type">
      <br>
      <input v-model="codeNumber" placeholder="" type="text">
      <img :src="code">
      <p id="look"></p>
      <p @click="getCode()" id="change"></p>
      <p class="tips"></p>
      <p class="tips"></p>
      <button @click="login()" id="login"></button>
      <router-link to="/reset" id="reset">?</router-link>
      <div id="key">
        <el-switch v-model="value" active-color="#13ce66" inactive-color="#ff4949" @click="show()"></el-switch>
      </div>
      <div>
        <el-dialog width="3rem" id="tipD" :visible.sync="dialogVisible">
          <!-- <el-image style="width:1rem;margin-left: 0.7rem;" src="../../../../static/img/tips.png"></el-image> -->
          <p id="please" :v-model="tips">{{tips}}</p>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="dialogVisible = false"></el-button>
          </span>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
        tips:"",
      dialogVisible: false,
      value: false,
      type: "password",
      code: "", //base64
      username: "",
      password: "",
      codeNumber: "" //
    };
  },
  watch: {
    value: function(val, old) {
      //val  old
      if (val == true) {
        this.type = "text";
      } else {
        this.type = "password";
      }
    }
  },
  created() {
    //
    this.getCode();
  },
  methods: {
       back(){
          this.$router.back();
      },
    show() {
      this.value = !this.value;
    },
    getCode() {
      const api = "https://elm.cangdu.org/v1/captchas";
      this.$http({
        url: api,
        method: "post",
        //cookies --- 
        withCredentials: true
      }).then(res => {
        // console.log("");
        // console.log(res);
        this.code = res.data.code;
      });
    },
    login() {
      const api = "https://elm.cangdu.org/v2/login";
      this.$http({
        url: api,
        method: "post",
        withCredentials: true,
        data: {
          username: this.username,
          password: this.password,
          captcha_code: this.codeNumber
        }
      }).then(res => {
        // console.log("login");
        // console.log(res);
        if (this.username=="") {
            this.dialogVisible=true;
            this.tips="";
        } else if(this.password==""){
            this.dialogVisible=true;
            this.tips="";
        }
        else if(this.codeNumber==""){
            this.dialogVisible=true;
            this.tips="";
        }else if(res.data.message){
            this.dialogVisible=true;
            this.tips=res.data.message;
        }
        else{
          this.$store.commit("getUsersData", res.data);
          this.$store.commit("boll", true);
          this.$store.commit("getUserName", res.data.username);
          //,queryparams
          this.$router.push({
            name: "city",

            query: res.data
          });
        }
      });
    }
  }
};
</script>
<style scoped>
#title {
  background-color: blue;
  padding: 0.1rem;
}
#title > img {
  width: 0.2rem;
}
#title > span {
  color: white;
  margin-left: 1.2rem;
}
#center {
  margin: 0.1rem 0;
  position: relative;
}
#center input {
  width: 3.55rem;
  padding: 0.1rem;
  height: 0.3rem;
  line-height: 0.3rem;
  border-bottom: 0.01rem solid gray;
  border-top: 0.01rem solid gray;
}
#center img {
  position: absolute;
  left: 2.3rem;
  top: 1.1rem;
}
#look {
  font-size: 0.15rem;
  position: absolute;
  right: 0.03rem;
  top: 1.1rem;
}
#change {
  font-size: 0.15rem;
  position: absolute;
  right: 0.03rem;
  top: 1.35rem;
}
.tips {
  font-size: 0.15rem;
  color: red;
  margin: 0.15rem;
}
#login {
  width: 3.45rem;
  background-color: lightgreen;
  padding: 0.1rem;
  border: none;
  outline: none;
  margin-left: 0.15rem;
  color: white;
  font-size: 0.2rem;
}
#reset {
  font-size: 0.18rem;
  position: absolute;
  right: 0.15rem;
  bottom: -0.3rem;
}
#key {
  position: absolute;
  right: 0rem;
  top: 0.67rem;
}
#please{
    font-size: 0.2rem;
}
</style>


