<template>
  <div style="background-color:#f1f1f1;">
    <header
      style="width: 100%; height: 0.5rem;font-size: 0.175rem;line-height: 0.5rem;background-color: #3190e8;color: #ffffff;text-align: center;"
    >
      <span class="leftspan">
       <van-icon @click="goback()" name="arrow-left"  style="font-size:0.3rem;width: 13%;line-height: 0.5rem;float: left;" color="white" />
      </span>
      <p
        class="tittle"
        style="width: 87%;font-size: 0.2rem;text-align: center;font-weight: bold;"
      ></p>
    </header>
    <div
      style="text-align:left;width: 100%; height: 0.5rem;font-size: 0.175rem;line-height: 0.5rem;background-color: #f2f2f2;color: #666666;text-align: center;"
    >
      <span style="margin-left:0.15rem;float:left;">:</span>
      <span style="margin-left:0.05rem;float:left;color:black;">{{$store.state.userName}}</span>
    </div>
    <div>
      <el-input placeholder="" v-model="acc" clearable></el-input>
      <el-input placeholder="" v-model="psw" clearable></el-input>
    </div>
    <div style="margin-top:0.3rem;">
      <el-button @click="open" style="width:3.55rem;margin: 0 0.1rem 0 0.1rem;" type="success"></el-button>
    </div>
    <div style="font-size:0.12rem;color:#AAAAAA;margin-top:0.3rem;">
      <p style="text-align:center;"></p>
      <p
        style="line-height:0.2rem;padding-left:0.5rem;margin-top:0.3rem;text-align:left;"
      ></p>
      <p style="line-height:0.2rem;padding-left:0.5rem;text-align:left;">30</p>
      <p style="line-height:0.2rem;padding-left:0.5rem;text-align:left;">90</p>
      <p style="line-height:0.2rem;padding-left:0.5rem;text-align:left;">360</p>
      <p
        style="line-height:0.2rem;padding-left:0.5rem;text-align:left;"
      >3431</p>
    </div>
  </div>
</template>
<script>
export default {
  name: "useCart",
  data() {
    return {
      acc: "",
      psw: ""
    };
  },
  created() {},
  methods: {
    goback() {
      this.$router.back();
    },
    open() {
      this.$message("");
    }
  }
};
</script>



