<template>
  <div>
    <div id="title">
      <img src="../../../../static/img/back.png" alt @click="back()">
      <span></span>
    </div>
    <div id="center">
      <input v-model="username" placeholder="" type="text">
      <br>
      <input v-model="oldpass" placeholder="" :type="password">
      <input v-model="password" placeholder="" :type="password">
      <input v-model="conpass" placeholder="" :type="password">
      <br>
      <input v-model="codeNumber" placeholder="" type="text">
      <img :src="code">
      <p id="look"></p>
      <p @click="getCode()" id="change"></p>
      <button @click="reset()" id="login"></button>
    </div>
    <div>
      <el-dialog width="3rem" id="tipD" :visible.sync="dialogVisible">
        <p id="please" :v-model="tips">{{tips}}</p>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  name: "reset",
  data() {
    return {
      tips: "",
      dialogVisible: false,
      code: "", //base64
      username: "",
      oldpass: "",
      password: "",
      conpass: "",
      codeNumber: "" //
    };
  },
  created() {
    //
    this.getCode();
  },
  methods: {
    back() {
      this.$router.back();
    },
    getCode() {
      const api = "https://elm.cangdu.org/v1/captchas";
      this.$http({
        url: api,
        method: "post",
        //cookies --- 
        withCredentials: true
      }).then(res => {
        // console.log("");
        // console.log(res);
        this.code = res.data.code;
      });
    },
    reset() {
      const api = "https://elm.cangdu.org/v2/login";
      this.$http({
        url: api,
        method: "post",
        withCredentials: true,
        data: {
          username: this.username,
          oldpassWord: this.oldpass,
          newpassword: this.password,
          confirmpassword: this.conpass,
          captcha_code: this.codeNumber
        }
      }).then(res => {
        // console.log("login");
        // console.log(res);
        if (res.data.message) {
          this.dialogVisible = true;
          this.tips = res.data.message;
        } else {
          //,queryparams
          this.$router.push({
            name: "login",
            query: res.data
          });
        }
      });
    }
  }
};
</script>
<style scoped>
#title {
  background-color: blue;
  padding: 0.1rem;
}
#title > img {
  width: 0.2rem;
}
#title > span {
  color: white;
  margin-left: 1.2rem;
}
#center {
  margin: 0.1rem 0;
  position: relative;
}
#center input {
  width: 3.55rem;
  padding: 0.1rem;
  height: 0.3rem;
  line-height: 0.3rem;
  border-bottom: 0.01rem solid gray;
  border-top: 0.01rem solid gray;
}
#center img {
  position: absolute;
  left: 2.3rem;
  top: 2.2rem;
}
#look {
  font-size: 0.15rem;
  position: absolute;
  right: 0.03rem;
  top: 2.2rem;
}
#change {
  font-size: 0.15rem;
  position: absolute;
  right: 0.03rem;
  top: 2.4rem;
}
.tips {
  font-size: 0.15rem;
  color: red;
  margin: 0.15rem;
}
#login {
  width: 3.45rem;
  background-color: lightgreen;
  padding: 0.1rem;
  border: none;
  outline: none;
  margin-left: 0.15rem;
  margin-top: 0.1rem;
  color: white;
  font-size: 0.2rem;
}
</style>


